
.feedback-form {
    text-align: center;
    max-width: 1000px;
    margin-left: auto;
    margin-right: auto;
    /* display: table;
    margin-left: auto;
    margin-right: auto; */
    /* animation-name: movertopbotom;
    animation-duration: 1.5s;
    animation-delay: 0s;
    object-fit: contain; */
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 10px;
    padding-bottom: 10px;
    /*animation-name: moveright;*/
    /*animation-duration: 1s;*/
    /*animation-delay: 0s;*/
}

.feedback-form-form {
    background-color: #344263;
    border-radius: 10px;
    padding-top: 5px;
    padding-bottom: 10px;
}

.feedback-form-divs {
    margin-left: auto;
    margin-right: auto;
}

.feedback-form-text-title {
    padding-top: 5px;
    font-size: 130%;
    /* padding-bottom: 5px; */
}

.feedback-form-input-error {
    background-color: #63000063;
    border-radius: 5px;
    text-align: center;
    margin: auto;
    color: red;
    /* font-weight: bold; */
}

.form-control::placeholder{
    color: rgba(179, 179, 179, 0.904);
}

@media (max-width: 930px) {

    .feedback-form-input-error {
        width: 90%;
    }    

    .feedback-form-divs {
        width: 90%;
    }
}

@media (min-width: 930px) {

    .feedback-form-input-error {
        width: 60%;
    }    

    .feedback-form-divs {
        width: 60%;
