.Message-body {
    animation-name: messmovertopbottom;
    animation-duration: 0.5s;
    animation-delay: 0s;
    background-color: #353a46;
    border-style: solid;
    border-color: #242730;
    position: absolute;
    border-radius: 7px;
    width: auto;
    height: auto;
    text-align: center;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    /* margin-left: auto; 
    margin-right: auto;
    margin: auto;  */
}

.Message-text {
    font-size: 200%;
    margin-top: 10px;
    margin-left: 10px;
    margin-right: 10px;
    margin-bottom: 10px;
}

.Message-btn {
    margin-top: 10px;
    margin-left: 10px;
    margin-right: 10px;
    margin-bottom: 10px;  
}

@keyframes messmovertopbottom {
    from { transform: translateY(-600%); }
    to   { transform: translate(-50%, -50%); }
}

@media (max-width: 930px) {

    .Message-text {
        font-size: 100%;
    }
}

@media (min-width: 930px) {

    .Message-text {
        font-size: 200%;
    }
}