
.social-hub {
    text-align: center;
    max-width: 1000px;
    margin: auto;
    /* padding-top: 2%; */
    /* display: inline-block; */
    /*animation-name: movertopbotom;*/
    /*animation-duration: 2s;*/
    /*animation-delay: 0s;*/
}

.social-icon:hover {
    background: white;
    color: #67c3da;
    /* color: rgba(256, 256, 256, 1); */
    /* border: 1px solid rgba(256, 256, 256, 1); */
    text-decoration: none;
    animation-name: shaking;
    animation-duration: 0.2s;
    animation-delay: 0s;
    animation-iteration-count: infinite;
}

.social-icon:before {
    display: inline-block;
    text-decoration: none;
}

.social-icon {
    background: #67c3da;
    color: white;
    padding: 15px;
    font-size: 300%;
    font-size-adjust: auto;
    width: 7vw;
    height: auto;
    border-radius: 7%;
    /* text-decoration: none; */
    margin: 5px 5px;
    min-height: 60px;
    min-width: 75px;
    /* text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black; */
}
