
.about-me {
    text-align: center;
    padding-top: 5px;
    /*animation-name: photo;*/
    /*animation-duration: 1s;*/
    /*animation-delay: 10s;*/
    /*animation-iteration-count: 2;*/
    object-fit: contain;
}

@keyframes photo {
    from { transform: translateY(-5px); }
    to   { transform: translateY(0px); }
}

@keyframes movertopbotom {
    from { transform: translateY(-400%); }
    to   { transform: translateY(0px); }
 }

.about-me-my-photo-img {
    width: 35vw;
    height: auto;
    /* max-width: 100%;
    height: auto; */
    border-radius: 100%;
    border: 3px solid #67c3da;
    box-shadow: 0 0 7px #666666;
    max-width: 500px;
    max-height: 500px;
}

.about-me-stext {
    padding-top: 2%;
    margin-left: 5px;
    margin-right: 5px;
    font-size: 130%;
    /* padding-bottom: 2%; */
}