
.header {
    /* background: #282c34; */
    background: linear-gradient(0deg, #282c349f 0%, #282c34 100%);
    /* background: linear-gradient(0deg, rgba(40,44,52) 0%, rgb(71, 82, 104) 100%); */
    /* position: absolute;
    align-items: center; */
    position: fixed;
    display: flex;
    left: 0; top: 0;
    width: 100%;
    height: 35px;
    /* min-height: 20px;
    object-fit: contain; */
    /* display: inline-block; */
    /* display: flex; */
    /* font-family: -apple-system, BlinkMacSystemFont, sans-serif; */
    animation-name: moveHeaderDown;
    animation-duration: 0.5s;
    animation-delay: 0s;
}

@keyframes moveHeaderDown {
    from { transform: translateY(-50px); }
    to   { transform: translateY(0px); }
}

.header-title-div {
    /* position: relative;  */
    /* font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif; */
    /* font-size: 200%; */
    /* padding-top: 0.1%; */
    padding-top: 5px;
    padding-left: 5px;
    text-align: left;
    /* padding: 20%; */
    /* position: relative; */
}

.header-title-text { 
    font-size: 100%;
}

.header-lang-menu {
    position: relative;
    /* right: 0; top: 0; */
    height: 35px;
    min-height: 20px;
    margin-left: auto;
    /* vertical-align: middle; */
    /* display: inline-block; */
    text-align: right;
    padding-top: 5px;
    padding-bottom: 5px;
    padding-left: 5px;
    padding-right: 5px;
}

.header-lang-span {
    padding-left: 10px;
    padding-right: 10px;
    font-size: 100%;
}

.header-lang-selector {
    background: linear-gradient(0deg, #282c349f 0%, #282c34 100%);
    border-radius: 5%;
    padding-left: 10px;
    padding-right: 10px;
    font-size: 100%;
    color: white;
}

.header-lang-selector:hover {
    cursor: pointer;
}

.header-lang-option {
    background-color: #282c34;
    padding-left: 10px;
    padding-right: 10px;
}