
.spec {
    text-align: center;
    padding-top: 5px;
    object-fit: contain;
}

.spec-text {
    padding-top: 2%;
    margin-left: 5px;
    margin-right: 5px;
    font-size: 130%;
}

.spec-link {
    color: #61dafb;
}

.spec-link:hover {
    color: #61dafb;
}

.spec-info {
    display: inline-grid;
    padding: 10px;
}

.spec-info-item {
    font-size: 130%;
    text-align: left;
}