
.project {
    /* text-align: center; */
    vertical-align: middle;
    padding-top: 1%;
    padding-bottom: 1%;
    /*animation-name: moveright;*/
    /*animation-duration: 1s;*/
    /*animation-delay: 0s;*/
    border-radius: 5px;
    border-color: #353a46;
    border-style: solid;
}

.project-content {
    max-width: 1000px;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
}

.project-img {
    display: inline-block;
    max-width: 200px;
    width: 20vw;
    height: auto;
    padding-left: 1%;
    padding-right: 1%;
    padding-top: 1%;
    padding-bottom: 1%;
    vertical-align: top;
}

.another-block {
    background-color: #353a46;
}

.project-text {
    display: inline-block;
    vertical-align: middle;
    max-width: 900px;
    font-size: 130%;
    text-align: left;
    padding-left: 2px;
    padding-right: 2px;
    padding-top: 2px;
    padding-bottom: 2px;
    width: 70vw;
    height: auto;
}
