
.footer {
    /* background: rgb(40,44,52); */
    /* background: linear-gradient(0deg,  rgb(71, 82, 104) 0%,  rgba(40,44,52) 100%); */
    background: linear-gradient(0deg,  #282c34 0%,  #282c349f 100%);
    /* background-position: 100%; */
    position: fixed;
    left: 0; bottom: 0;
    width: 100%;
    height: 35px;
    /* min-height: 25px; */
    /* font-family: -apple-system, BlinkMacSystemFont, sans-serif; */
    /* text-align: right; */
    /* position: absolute;
    align-items: center; */
    /* vertical-align: middle; */
    /* object-fit: contain; */
    /*display: flexbox;*/
    animation-name: moveHeaderUp;
    animation-duration: 0.5s;
    animation-delay: 0s;
}

@keyframes moveHeaderUp {
    from { transform: translateY(50px); }
    to   { transform: translateY(0px); }
}
.footer-createdOnReact {
    /* position: fixed; */
    position: relative;
    text-align: right;
    /* display: inline-block; */
    /* vertical-align: middle; */
    /* bottom: 0; */
    /* right: 0; */
    padding-top: 5px;
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 1px;
}

.footer-crtext {  
    padding-left: 2px;
    padding-right: 2px;
    font-size: 100%;
}

.footer-react-logo { 
    width: 25px;
    height: 25px;
}

@media (prefers-reduced-motion: no-preference) {
    .footer-react-logo {
      animation: footer-react-logo-spin infinite 10s linear;
    }
}

@keyframes footer-react-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
}